  border: none;
  border-radius: .8em;
  font-family: Lobster, cursive;
  font-size: 18px;
  font-weight: 500;
  position: relative;
  overflow: hidden;
}

.hidden {
  display: none;
}

.cssbuttons-io span {
  z-index: 10;
  align-items: center;
  padding: .8em 1.2em .8em 1.05em;
  transition: color .4s;
  display: inline-flex;
  position: relative;
}

.cssbuttons-io:before, .cssbuttons-io:after {
  width: 100%;
  height: 100%;
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.cssbuttons-io:before {
  content: "";
  width: 120%;
  background: #000;
  transition: transform .4s cubic-bezier(.3, 1, .8, 1);
  left: -10%;
  transform: skew(30deg);
}

.cssbuttons-io:hover:before {
  transform: translate3d(100%, 0, 0);
}

.cssbuttons-io:active {
  transform: scale(.95);
}

